import React, { Component } from 'react'

export default class bookcase extends Component {
    constructor(props){
        super(props)
        
    }
    render() {
        return (
            <div className="bbox">
                <div className="nav"></div>
                <div className="menu">
                    <div className="menuitem"></div>
                </div>
                <div className="menulist">
                    <div className="listitem" >
                        <div className="boxL">
                            <div className="Lname"></div>
                            <div className="Lprice"></div>
                        </div>
                        <div className="boxR"></div>
                    </div>
                </div>
            </div>
        )
    }
}
